import React from "react";
import { injectIntl } from "react-intl";
import Page from "material-ui-shell/lib/containers/Page/Page";
import Scrollbar from "material-ui-shell/lib/components/Scrollbar";
import { Card, CardHeader } from "@material-ui/core";

import data from "../../data/msf";

const { education } = data;

const renderEducation = () => {
  return education.map(({ id, course, university, from, to }) => {
    return (
      <Card key={id}>
        <CardHeader
          action={
            <p>
              {from} - {to}
            </p>
          }
          title={course}
          subheader={university}
        />
      </Card>
    );
  });
};

const EducationPage = ({ intl }) => {
  return (
    <Page pageTitle={intl.formatMessage({ id: "education" })}>
      <Scrollbar
        style={{ height: "100%", width: "100%", display: "flex", flex: 1 }}
      >
        {/* {intl.formatMessage({ id: 'education' })} */}
        {renderEducation()}
      </Scrollbar>
    </Page>
  );
};

export default injectIntl(EducationPage);
